.footer{
    background-color: rgba(21, 21, 224, 0.6);
    position: relative;
    bottom: 0;
    z-index: 5;

    .footer-inner{
        padding: 20px 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &-right{
        &-top{
            margin-bottom: 30px;
            img{
                margin-right: 20px;

                &:last-child{
                    margin-right: 0;
                }
            }
        }

        &-bottom{
            &_info{
                font-style: normal;
                font-weight: normal;
                font-size: 16px;
                line-height: 20px;
                color: #fff;
            }
        }
    }

    &-info{
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 20px;
        color: #fff;
    }

    .logo{
        width: 150px;
        height: 30px;
        margin-bottom: 30px;
    }
}