.header{
    background-color: rgba(21, 21, 224, 0.6);
    margin-bottom: 40px;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 15;

    &-inner{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__link{
        padding: 25px;
        display: inline-block;
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 17px;
        color: #fff;

        &:hover{
            text-shadow: 4px 4px 20px rgb(207, 195, 195);
            color: #fff;
        }
    }

    .input{
        padding: 8px 8px  8px 16px;
        box-sizing: border-box;
        border-radius: 10px;
        border: none;
        outline: none;
        background-color: transparent;
        color: #fff;
        font-size: 14px;
    }

    .input::placeholder{
        color: #fff;
        font-size: 14px;
    }

    &-right{
        padding: 5px 0 5px 35px;
        box-sizing: border-box;
        border-radius: 20px;
        background-color: rgba(255, 255, 255, 0.5);
        position: relative;
        border: 1px solid #fff;
        margin-right: 8px;

        &:focus{
            background-color: #fff;
        }

        .input:focus &{
            background-color: #fff;
        }

        &::before{
            content: '';
            position: absolute;
            width: 20px;
            height: 20px;
            top: 0;
            bottom: 0;
            left: 20px;
            margin: auto;
            background-image: url(../../assets//icons/search.svg);
            background-repeat: no-repeat;
        }
    }

    .logo{
        width: 150px;
        height: 30px;
    }
}