.artist-card{
    width: 800px;
    height: 240px;
    display: block;
    margin-right: 40px;
    img{
        width: 200px;
        height: 100%;
        object-fit: cover;
    }

    .actor-name{
        color: #000;
        font-size: 18px;
        margin-bottom: 5px;
        margin-top: 10px;
    }

    h3{
        color: #000;
        font-size: 16px;
    }
}