.movie-card {
    width: 250px;
    margin-bottom: 30px;
    box-shadow: 0 0 5px 4px grey;
    cursor: pointer;
    position: relative;
    color: #fff;

    .card-rating{
        position: absolute;
        right: 0;
        top: 20px;
        padding: 7px 20px 7px 10px;
        z-index: 3;
        color: #fff;
        background-color: #8468f0;
        border-top-left-radius: 5px;
        border-bottom-left-radius: 5px;
    }

    &:hover &-top__img{
        transform: scale(1.2);
        box-shadow: 2px 0 9px 6px grey;
    }

    &-content{
        padding: 10px;
        box-sizing: border-box;

        &__title{
            font-size: 16px;
            font-weight: bold;
            margin-bottom: 10px;
        }

        .release-date{
            margin-bottom: 15px;
        }

        &__link{
            font-style: normal;
            font-weight: 600;
            font-size: 14px;
            line-height: 16px;
            padding: 8px 12px;
            background: #5931AC;
            border-radius: 4px;
            display: inline-block;
            text-align: left;
            margin-right: 16px;
            box-sizing: border-box;
            color: #FFFFFF;
        }
    }

    &-top{
        width: 100%;
        overflow: hidden;
        height: 350px;
        margin-bottom: 10px;
        

        &__img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
            transform: scale(1.0);
            transition: all ease 0.5s;
        }
    }
}

.info{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}