.movie-info{

    display: flex;

    .info-right{
        position: relative;
        z-index: 2;
        display: flex;
        align-items: center;
        background-color: rgba(255, 255, 255,0.5);
        backdrop-filter: blur(10px);
    }

    .movie-poster{
        margin-right: 50px;
        height: 100%;
        width: 500px;
        object-fit: cover;
    }

    .genre{
        display: flex;

        h4{
            margin-right: 20px;
            font-size: 22px;
            font-weight: bold;
            color: #fff;
            text-shadow: 2px 2px 2px red;
            margin-right: 20px;
        }

        button{
            margin-right: 10px;
        }
    }

    .main-title{
        font-size: 22px;
        font-weight: bold;
        color: #fff;
        text-shadow: 2px 2px 2px red;
        margin-right: 20px;
    }

    

    .movie-title, .tagline, .movie-overview, .movie-budget, .runtime, .release, .offi-page, .genre{
        margin-bottom: 10px;
        // text-shadow: 2px 2px 3px #fff;
        color: #000;
    }

    .movie-overview{
        width: 500px;
        font-size: 16px;
        font-weight: bolder;
        line-height: 30px;
    }

    .offi-page{
        display: inline-block;
        font-size: 22px;
        font-weight: bold;
        color: #fff;
        text-shadow: 2px 2px 2px red;
        margin-right: 20px;

        button{
            margin-left: 10px;
        }
    }

    .front-info{
        margin-right: 30px;
        padding: 30px 0 0 30px;
    }

    .info-left{
        img{
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            margin: auto;
            object-fit: cover;
            filter: blur(10px);
        }
    }
}

.actors{
    // height: 300px;
    // width: 600px;
    display: flex;
    width: 500px;
    overflow-x: scroll;
    height: 360px;
}

.actor-title{
    margin-bottom: 20px;
    color: #fff;
    text-shadow: 2px 2px 2px #000;
    border: 2px solid #000;
    display: inline-block;
    padding: 5px;
    box-sizing: border-box;
    background-color: #000;
}

.advise-block{
    display: flex;
    margin-bottom: 50px;
    justify-content: space-between;
    width: 1100px;
    overflow-x: scroll;
    height: 404px;
}

.advise-title{
    margin-bottom: 20px;
    margin-top: 49px;
    color: #fff;
    position: relative;
    z-index: 20;
}

.App{
    margin-top: 100px;
}